			</view>
			
			<view class="action-btn-group">
				<button type="primary" class=" action-btn no-border buy-now-btn" id="buy" @click="buy">我要租</button>
				<!-- <button type="primary" class=" action-btn no-border add-cart-btn"  id="rentCart"  @click="rentCart">加入租单</button> -->
