<script>
	
	import times from '@/components/pretty-times/pretty-times.vue'
	import store from '@/store/index.js';
	import share from '@/components/share';
	import {
	    mapState,mapMutations
	} from 'vuex';
	export default{
		components: {
			share,
			times
		},
		data() {
			return {
				specClass: 'none',
				specSelected:[],
				goodsOne: [],
				favorite: true,
				shareList: [],
				imgList: [],
				desc: '',
				specList: [],
				specChildList: [],
				buyinfo:[],
				from : 2,//直接下单的
				begin_time: '',//预约开始时间
				end_time: '',//预约结束时间
				button: true,
				canbuy: true,
				appointTime: ["2021-11-16 15:30:00"],
				// disableTime: [
				// 	{ 'startTime': '11：00', 'endTime': '12：00' },
				// 	{ 'startTime': '13：00', 'endTime': '14：00' }
				// ],
				disableTimeSlot: {//需要改为多时间段！
					begin_time: "2021-11-17 11:00:00",
					end_time: "2021-11-20 11:00:00"
					
				}
			};
		},
		
		async onLoad(options){
			console.log('test');
			//接收传值,id里面放的是标题，因为测试数据并没写id 
			let id = options.id;
			if(id){
				this.$api.msg(`点击了${id}`);
			}
			this.loadGoods(id);
			// console.log('loadGoods');
			// console.log(this.specList);
			//规格 默认选中第一条
			this.shareList = await this.$api.json('shareList');
			console.log('goods');
		},
		computed:{
			...mapState(['hasLogin','userInfo']),
			// this.loadData();
		},
		methods:{
			...mapMutations(['editCart']),
			//获得商品数据
			async loadGoods(id){
				console.log('应该把用户信息传过去如果有则获取收藏的');
				console.log(JSON.stringify(this.userInfo));
				let userInfo  = this.userInfo;
				
				
				const sendData = { //根据分类得到相应的 状态订单
					id,
					userInfo
				};

				
				const res = await this.$myRequest({url: '/api/goods/goodsOne',method:'post',data:sendData});
				// const res = await this.$myRequest({url: '/api/goods/goodsOne?id='+id});
								 console.log('商品页');
								 console.log(res.data);
								this.goodsOne = res.data.res;
								this.imgList = JSON.parse(res.data.res.image);//主图
								// console.log('goodsimg');
								// console.log(this.imgList);				
								this.desc = res.data.res.content;
								this.favorite = res.data.res.collection;
								console.log('attr_value:');
								console.log(res.data.attr_value);
								let tempattr = JSON.parse(res.data.attr_value);
								console.log(tempattr['specList']);
								this.specList = tempattr['specList'];
								this.specChildList = tempattr['specChildList'];

					//规格 默认选中第一条
					this.specList.forEach(item=>{
							for(let cItem of this.specChildList){
								if(cItem.pid === item.id){
									console.log('cItem:'+cItem);
									this.$set(cItem, 'selected', true);
									this.specSelected.push(cItem);
									break; //forEach不能使用break
								}
							}
					})
					
					const res2 = await this.$myRequest({url: '/api/goods/getAttrInfo',method:'post',data: this.specSelected});
						console.log('getAttrInfo:');
						console.log(res2.data);
						console.log('库存信息');
						console.log(res2.data.stock);
						let stock = res2.data.stock;
						this.goodsOne.price = stock.price;//显示价格
						this.goodsOne.stock = stock.stock;//显示库存
						console.log('stock:'+stock.stock);
						this.buyinfo = stock;

				  // console.log(ttt);
				// let goodsList = await this.$api.json('goodsList');
				// this.goodsList = goodsList || [];
			},
			//规格弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					
					
					console.log(e)
					
					
					
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//选择规格
			async selectSpec(index, pid){
				console.log('selectSpec:'+JSON.stringify(this.specChildList));
				let list = this.specChildList;
				list.forEach(item=>{
					if(item.pid === pid){
						this.$set(item, 'selected', false);
						// console.log('这是上下分类选中这个:'+pid);
						console.log(item);
					}
				})

				this.$set(list[index], 'selected', true);
				
				console.log('list[index]:'+JSON.stringify(list[index]));//第一个栏目选中的值
				//存储已选择
				/**
				 * 修复选择规格存储错误
				 * 将这几行代码替换即可
				 * 选择的规格存放在specSelected中
				 */
				this.specSelected = []; 
				list.forEach(item=>{ 
					if(item.selected === true){ 
						this.specSelected.push(item); 
						
					} 
				})
				console.log('specSelected:'+ JSON.stringify(this.specSelected));//可以根据组合去获得相应的价格。
				
				
				const res = await this.$myRequest({url: '/api/goods/getAttrInfo',method:'post',data: this.specSelected});
					console.log('getAttrInfo:');
					console.log(res.data);
					console.log('库存信息');
					console.log(res.data.stock);
					let stock = res.data.stock;
					this.buyinfo = res.data.stock;
					this.goodsOne.price = stock.price;//显示价格
					this.goodsOne.stock = stock.stock;//显示库存
					console.log('stock.pricc'+stock.price);
					console.log('stock:'+stock.stock);
					this.goodsOne.thumb = stock.thumb;
					console.log('this.goodsOne.thumb:'+this.goodsOne.thumb);
					 if(stock.stock >= 1){//什么情况可以购买
						this.button = true;
						console.log('重置');
						console.log(this.button);
						this.canbuy = true;
					 }else{
						 this.button = false;
						 this.canbuy = false;
					 }
					
					
				
				
			},
			//分享
			share(){
				this.$refs.share.toggleMask();	
			},
			//收藏
			async toFavorite(){
				this.if_login();
				console.log('收藏与否:');
				console.log('this.goodsOne:'+JSON.stringify(this.goodsOne));
				console.log(this.favorite);
				console.log(this.hasLogin);
				if(!this.hasLogin){//如果没有登陆则提示要登陆。 或者直接去登陆页面。
					console.log('没有登陆。请登陆！');
					this.$api.msg('登陆后再收藏');
					uni.navigateTo({
						url: '/pages/public/login'
					})
					return;
				}
				let  goodsOne = this.goodsOne;
				let  userInfo = this.userInfo;
				const sendData = { //根据分类得到相应的 状态订单
					goodsOne,userInfo
				};
				const res = await this.$myRequest({url: '/api/goods/collectionGoods',method:'post',data:sendData});	//获取分类。可以缓存
				console.log(res.data);
				
				if(res.data.res.fav == true){
					this.$api.msg('收藏了');
					this.favorite = true;
				}else{
					this.$api.msg('取消收藏');
					this.favorite = false;
				}
				// this.favorite = !this.favorite;
			},
			if_login(){
				if(!this.hasLogin){//如果没有登陆则提示要登陆。 或者直接去登陆页面。
					console.log('没有登陆。请登陆！');
					this.$api.msg('登陆后再收藏');
					uni.navigateTo({
						url: '/pages/public/login'
					})
					return;
				}
			},
			getTime(e) {//改成到这里
				if(this.specClass === 'show'){
					
					
					console.log(e.beginTime)
					console.log(e.endTime)
					
					this.begin_time = e.beginTime;
					
					
					let goodsData =  [];
					let  goodsOne = this.goodsOne;
					let  userInfo = this.userInfo;
					let  buyinfo  = this.buyinfo;
					let  from     = this.from;
					let  begin_time = this.begin_time;
					console.log('begin_time');
					console.log(begin_time);
					 goodsData.push({
					 	attr_val: buyinfo.attr,
					 	number: goodsOne.number,
					 	id: goodsOne.id,
						image: '',
						number: 1,
					 });
					 
					 
					 console.log('test:');
					 console.log(goodsData);
					
					
					console.log('goodsData::::'+JSON.stringify(goodsData));
					uni.navigateTo({ 
					url: `/pages/order/createOrder?data=${JSON.stringify({
						goodsData: goodsData , userInfo: userInfo , from: from , startTime: begin_time
					})}`
					})
					
					
					
					
					//this.specClass = 'hide';
					// setTimeout(() => {
					// 	this.specClass = 'none';
					// }, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			buy(){
				this.if_login();//没有登陆先登陆再说。
				console.log('buy?');
				
				if(!this.canbuy){
					this.$api.msg('暂时不能预定哦');
					return 0;
				}
				if(this.begin_time === ''){
					this.specClass = 'show';
					
					return;
				}
				let goodsData =  [];
				let  goodsOne = this.goodsOne;
				let  userInfo = this.userInfo;
				let  buyinfo  = this.buyinfo;
			    let  from     = this.from;
				let  begin_time = this.begin_time;
				 goodsData.push({
				 	attr_val: buyinfo.attr,
				 	number: goodsOne.number,
				 	id: goodsOne.id,
					image: '',
					number: 1,
				 })
				 console.log('goodsData::::'+JSON.stringify(goodsData));
				uni.navigateTo({ 
				url: `/pages/order/createOrder?data=${JSON.stringify({
					goodsData: goodsData , userInfo: userInfo , from: from
				})}`
				})
			},
			async rentCart(){//加入购物车 如何也加入store
				this.if_login();
				console.log('这个加入购物车');
				if(!this.canbuy){
					this.$api.msg('库存不足啦');
					return 0;
				}
				// console.log(this.goodsOne);
				
				let  goodsOne = this.goodsOne;
				let  userInfo = this.userInfo;
				let  buyinfo  = this.buyinfo;
				const sendData = { //根据分类得到相应的 状态订单
					goodsOne,
					userInfo,
					buyinfo
				};
				
				const res = await this.$myRequest({url: '/api/cart/addCart',method:'post',data:sendData});	
				console.log('userInfo:::'+JSON.stringify(res.data.userInfo));
				console.log(res.data.res);
				this.editCart(res.data.res);
				console.log('这里看看看看看');
				
				
				
				return uni.showToast({
				    title: '加入了'
				})
				
			},
			stopPrevent(){}
		},

	}
</script>

